@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-width:thin;
}

a {
  text-decoration: inherit;
  color: inherit;
}

.App{
  min-height: 98.5vh;
  border: 8px solid #000e35;
  margin: 5px;
  padding: 5px;
}
.title {
    /* text-transform: uppercase; */
    font-size: 8vw;
    font-family: 'Oswald', sans-serif;
    cursor: pointer;
    color: #02278b;
  }
  
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .divider {
    margin: 10px;
    width: 100%;
    background-color: grey;
  }
.content {
    display: flex;
    justify-content: space-around;
    /* flex-direction: column-reverse; */
  }
  
  .settings {
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
  }
  
  .banner {
    width: 55%;
    align-self: center;
    padding: 8px;
  }
  
  @media (max-width: 800px) {
    .content {
      flex-direction: column-reverse;
      align-items: center;
    }
    .banner {
      width: 85%;
      padding: 0;
      padding-top: 20px;
    }
    .settings {
      width: 100%;
    }
  }
  
  .settings__select {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    flex: 0.8 1;
    justify-content: space-evenly;
  }
.quiz {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Raleway", sans-serif;
  }
  
  .subtitle {
    font-size: 25px;
    border: 1px solid black;
    border-radius: 20px;
    padding: 5px 10px;
    background-color: white;
  }
  
  .quizInfo {
    width: 100%;
    display: flex;
    text-transform: uppercase;
    font-weight: 600;
    justify-content:space-between;
  }

  .score{
    font-size: 40px;
    font-family: "Bebas Neue", sans-serif;
  }
.question {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .singleQuestion {
    width: 95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 5px solid grey;
    padding: 20px;
    margin-top: 10px;
  }
  
  .options {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
  }
  
  .singleOption {
    width: 46%;
    height: 50px;
    padding: 15px 20px;
    margin: 10px;
    border-width: 1px;
    border-radius: 20px;
    border-color: black;
  }
  
  @media (max-width: 614px) {
    .singleOption {
      width: 100%;
    }
  }
  
  .controls {
    display: flex;
    width: 100%;
    justify-content: space-around;
  }
  
  .select {
    background-color: rgb(7, 207, 0);
    color: white;
    border-width: inherit;
    border-radius: 20px;
    border-width: 1px;

  }
  
  .wrong {
    background-color: rgb(233, 0, 0);
    color: white;
    border-width: inherit;
    border-radius: 20px;
    border-width: 1px;

  }
.result {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60vh;
    text-align: center;
    font-family: 'Raleway', sans-serif;
  }

  .score_message{
    text-transform: uppercase;
    font-size: 4vw;
    font-family: 'Raleway', sans-serif;
    color: #02278b;
    
  }
